<template>
    <div v-intersect="onIntersect">
    <v-container class="py-16">
        <v-row class="align-center">
            <v-col cols="12" lg="6">
                <transition name="slide-left">
                    <h3 v-show="isVisible" class="text-h3 mb-4 text-center">Galeria</h3>
                </transition>
                <transition name="slide-upValue1">
                    <v-row class="px-4" v-show="isVisible">
                        <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit. Nulla luctus malesuada gravida. Nullam venenatis ante nec est vehicula, id suscipit sapien luctus. In condimentum eros elit, ac volutpat est euismod ac. Ut malesuada nulla sit amet lorem suscipit ultricies. Nulla aliquet nec ante sit amet suscipit. Aenean vitae lacinia neque, a hendrerit sem. Vivamus erat lectus, consectetur vel faucibus ut, scelerisque vitae ex. Sed metus ex, scelerisque at lacinia in, lacinia eu turpis. Duis pretium tempor mauris sit amet dignissim. Praesent non dui ut augue lacinia mattis. Maecenas tristique, lectus non suscipit sagittis, orci enim aliquam magna, nec euismod justo mauris vel dui.</p>
                    </v-row>
                </transition>
            </v-col>
            <v-col cols="12" lg="6">
                <v-row>
                    <v-col cols="4" v-for="(gallery, index) in filtredgalleries" :key="index" class="pa-0 gallery-item">
                    <v-hover v-slot="{ hover }">
                    <a href="#">
                    <v-img :src="gallery.src" :lazy-src="gallery.lazy" aspect-ratio="1">
                        <v-row :class="{'fill-height':true, 'justify-center':true, 'align-center':true, 'overlay':hover, 'onhover':true, 'darken-2':true, 'white--text':true}" style="transition: background .5s ease-in-out"><div class="text-h5 text-center">{{gallery.name}}</div></v-row>
                    </v-img>
                    </a>
                    </v-hover>
                </v-col>
                </v-row>
            </v-col>
        </v-row>
    </v-container>
    </div>
</template>

<script>
import gallery from '../db/galleries.json'
export default {
    name: 'gallery',
    data: () => ({
        isVisible:false,
        galleries: gallery.galleries,
        selected: 0,
        windowWidth: 0
    }),
    mounted(){
        this.windowWidth = window.innerWidth;
    },
    computed: {
        filtredgalleries(){
            var fg = []
            for (let index = 0; index < 9; index++) {
                const element = this.galleries[this.galleries.length-(index+1)];
                fg.push(element)
            }
            return fg
        }
    },
    methods: {
        onIntersect(entries) {
            if(!this.isVisible){
                this.isVisible = entries[0].isIntersecting
            }
        },

        swipeLeft(){
            if(this.selected != gallery.galleries.length){
                this.selected = this.selected + 1
            }
        },

        swipeRight(){
            if(this.selected != 0){
                this.selected = this.selected - 1
            }
        }
    }
}
</script>

<style>
    .gallery-item a {
        text-decoration: none;
    }

    .onhover {
        background: rgba(246, 165, 5, .3);
    }

    .overlay {
        background: rgba(246, 165, 5, .9);
    }

    
</style>