<template>
    <v-container class="py-16" style="color:#fff" v-intersect="onIntersect">
        <transition name="slide-left">
        <h3 class="text-h3 mb-4 text-center" v-show="isVisible">Poznaj nasz zespół</h3>
        </transition>
        <transition name="slide-upValue3">
        <v-row class="px-4" v-show="isVisible">
            <p style="text-align: center; width: 100%">Lorem ipsum dolor sit amet, consectetur adipiscing elit. Nulla luctus malesuada gravida.</p>
        </v-row>
        </transition>
        <transition name="slide-upValue5">
            <v-carousel hide-delimiters height="360">
                <v-carousel-item v-for="(slide, index) in slides" :key="index">
                    <v-row class="px-12 fill-height align-center">
                    <v-col cols="12" lg="3" md="6" class="justify-center" @click="toggle(index)" v-for="(member, index) in slide" :key="index">
                        <v-row class="justify-center">
                            <v-avatar size="192"><v-img aspect-ratio="2" :src="member.src" :lazy-src="member.lazy" /></v-avatar>
                        </v-row>
                        <v-row class="justify-center mt-4">
                            <h3>{{member.name}}</h3>
                        </v-row>
                        <v-row class="justify-center">
                            <h6>{{member.title}}</h6>
                        </v-row>
                        <v-row class="justify-center">
                            <v-btn icon dark v-for="(link,index) in member.social" :key="index"><v-icon>{{link.icon}}</v-icon></v-btn>
                        </v-row>
                    </v-col>
                    </v-row>
                </v-carousel-item>
            </v-carousel>
        </transition>
    </v-container>
</template>

<script>
import team from '../db/team.json'
export default {
    name: 'team',
    data: () => ({
        isVisible: false,
        team: team.team,
        windowWidth: 0
    }),
    mounted(){
        this.windowWidth = window.innerWidth;
    },
    computed: {
        slides(){
            var sl = []
            var members = []
            var size = 1
            if(this.windowWidth>1264){
                size = 4
            } else if (this.windowWidth>960){
                size = 2
            }
                for (let index = 0; index < this.team.length; index++) {
                    const element = this.team[index];
                    members.push(element)
                    if(members.length == size || this.team.length == index+1)
                    {   
                        sl.push(members)
                        members = []
                    }
                }
            return sl
        }
    },
    methods: {
        onIntersect(entries) {
            if(!this.isVisible){
                this.isVisible = entries[0].isIntersecting
            }
        },
    }
}
</script>