<template>
    <v-container class="py-16">
            <h3 class="text-h3 mb-4 text-center">Akcje charytatywne</h3>
            <v-row class="px-4">
                <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit. Nulla luctus malesuada gravida. Nullam venenatis ante nec est vehicula, id suscipit sapien luctus. In condimentum eros elit, ac volutpat est euismod ac. Ut malesuada nulla sit amet lorem suscipit ultricies. Nulla aliquet nec ante sit amet suscipit. Aenean vitae lacinia neque, a hendrerit sem. Vivamus erat lectus, consectetur vel faucibus ut, scelerisque vitae ex. Sed metus ex, scelerisque at lacinia in, lacinia eu turpis. Duis pretium tempor mauris sit amet dignissim. Praesent non dui ut augue lacinia mattis. Maecenas tristique, lectus non suscipit sagittis, orci enim aliquam magna, nec euismod justo mauris vel dui.</p>
            </v-row>
            <v-row>
                <v-col cols="12">
                <v-timeline v-if="actions" :dense="windowWidth < 600 ? true : false">
                    <v-timeline-item
                    v-for="(action, index) in actions"
                    :key="index"
                    color="info"
                    fill-dot
                    small
                    
                    >
                    <v-card class="elevation-2">
                        <v-img v-if="action.image" aspect-ratio="2" :src="action.src" :lazy-src="action.lazy"/>
                        <v-card-title class="headline">
                        {{action.title}}
                        </v-card-title>
                        <v-card-subtitle>{{action.date}}</v-card-subtitle>
                        <v-card-text>{{action.text}}</v-card-text>
                        <v-card-actions v-if="action.href">
                            <v-btn text small>czytaj dalej...</v-btn>
                        </v-card-actions>
                    </v-card>
                    </v-timeline-item>
                </v-timeline>
                <v-row :class="{'px-4':true,'justify-center': windowWidth < 600 ? false : true}">
                <v-btn outlined rounded color="info">Więcej</v-btn>
                </v-row>
                </v-col>
            </v-row>
    </v-container>
</template>

<script>
import charity from '../db/charity.json'

export default {
    name: 'charity',
    data: () => ({
        charity: charity.charity,
        more: false,
        isVisibleArticle: false,
        isVisibleArticle2: false,
        windowWidth: 0
    }),
    mounted(){
        this.checkWidth()
    },
    computed: {
        actions(){
            let actionsFiltred = []
            let length = 4
            if(this.charity.length < 4){
                length = this.charity.length
            }
            for (let index = 0; index < length; index++) {
                var position = this.charity.length-(index+1)
                var action = this.charity[position]
                actionsFiltred.push(action)
            }
            return actionsFiltred
        }
    },
    methods: {
        checkWidth(){
            this.windowWidth = window.innerWidth;
        },
        onIntersectArticle(entries){
            if(!this.isVisibleArticle){
                this.isVisibleArticle = entries[0].isIntersecting
            }
        },

        onIntersectArticle2(entries){
            if(!this.isVisibleArticle2){
                this.isVisibleArticle2 = entries[0].isIntersecting
            }
        }
    }
}
</script>