<template>
    <v-container>
        <v-row class="py-12" v-intersect="onIntersect">
            <v-col cols="6" lg="3" class="my-8" v-for="(counter, index) in counters" :key="index">
                <transition :name="'slide-upValue'+(index+1)">
                    <div v-show="isVisible" >
                <v-row class="display-2 justify-center" style="color:#fff">{{counter.value}}</v-row>
                <v-row class="justify-center" style="color:#fff">{{counter.text}}</v-row>
                    </div>
                </transition>
            </v-col>
        </v-row>
    </v-container>
</template>
<script>
export default {
    name: "counter",
    data: () => ({
        counters: [
            {
                value: 1234,
                text: 'jakiś opis'
            },
            {
                value: 1234,
                text: 'jakiś opis'
            },
            {
                value: 1234,
                text: 'jakiś opis'
            },
            {
                value: 1234,
                text: 'jakiś opis'
            },
        ],
        isVisible:false
    }),
    methods: {
        onIntersect(entries) {
            if(!this.isVisible){
                this.isVisible = entries[0].isIntersecting
            }
        }
    }
}
</script>